.locations {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 50vh;
    background-color: #F9FAFB;
    display: flex;
    justify-content: center;
    align-items: center;
}
.location-details {
    width: 90%;
    min-height: 80%;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 5vh;
}
.loc-head {
    left: 5vw;
    top: 5vh;
}
.loc {
    height: 29.8vh;
    position: relative;
    display: flex;
    justify-content: space-between;
}
