* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Segoe UI";
}
::-webkit-scrollbar {
    display: none;
}
.background {
    position: relative;
    margin: 0;
    padding: 0;
    height: 49vh;
    overflow: hidden;
}
.background img {
    width: 100%;
    height: 110%;
    background-repeat: no-repeat;
    background-size: cover;
    filter: blur(1px);
    filter: brightness(70%);
}
.background .navbar {
    display: flex;
    position: absolute;
    top: 3%;
    left:2%;
    width: 96%;
}
.navbar img {
    width: 6%;
    height: 6%;
    filter: brightness(100%);
}
.right-nav {
    display: flex;
    position: absolute;
    width: 95%;
    height: 100%;
    justify-content: flex-end;
    align-items: center;
    gap: 2vw;
    color: white;
}
.right-nav div {
    font-size: 2vh;
    padding: 0.5%;
    cursor: pointer;
}
.right-nav div:nth-child(4),.right-nav div:nth-child(5) {
    border: 1px solid white;
    border-radius: 12%;
    border-style: inset;
}
.background .text-name {
    display: flex;
    position: absolute;
    height: 20vh;
    left: 27.5vw;
    top: 20%;
}
.background .text-name span {
    color: white;
    font-family: "Segoe UI";
    font-size: 8vh;
    font-weight: 700;
}
.background .sub-head {
    position: absolute;
    top:20vh;
    left: 33.5vw;
    height: 2.5vh;
}
.background .sub-head .book  {
    color: white;
    font-size: 2vh;
}
.assurence {
    display: flex;
    position: absolute;
    left: -1.9vw;
    top: 5vh;
    gap: 3vw;
}
.assurence span {
    border: 1px solid white;
    font-size: 2vh;
    background-color: rgba(128, 128, 128, 0.6);
    width: 10vw;
    text-align: center;
    filter: opacity(0.6);
    color: white;
    border-style:ridge;
}
.search-bar {
    position: absolute;
    top: 30vh;
    left: 27.5vw;
    border: none;
    background-color: white;
    border-radius: 40px 40px;
    overflow: hidden;
    height: 7vh;
    opacity: 0.6;
    display: flex;
}
.search-bar input {
    text-decoration: none;
    border: none;
    width: 40vw;
    height: 100%;
    text-align: center;
    font-size: 1em;
    opacity: 0.8;
}
.search-bar input:focus {
    border: 0;
    outline: none;
}
.search-bar input::placeholder {
    text-align: center;
    text-size: 1em;
}
.ver {
    position: absolute;
    display: flex;
    text-align: center;
    bottom: 0;
}
.search-icon {
    width: 4.5vw;
    height: 100%;
    background-color: #ED3A56;
    display: flex;
    align-items: center;
    justify-content: center;
}