.steps-details {
    margin: 0;
    padding: 0;
    height: 50vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.step {
    width: 90%;
    height: 90%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.step-start {
    margin-top: 5vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 30vh;
    width: 100%;
}
.step-step {
    width: 15vw;
    height: 55%;
    padding: 2vh;
    border: 1px solid rgba(214, 213, 213, 0.678);
    border-radius: 6px;
    position: relative;
}
.arrow {
    width: 1.5vw;
    height: 2vh;
}
.arrow img {
    width: 100%;
    height: 100%;
}
.left {
    position: absolute;
    top: 35%;
    left: -12%;
    width: 2.5vw;
    height: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid  rgba(214, 213, 213, 0.678);
    border-radius: 50px;
    background-color: white;
    box-shadow: 3px 0 0 0 rgba(0, 0, 0, 0.103);
}
.left p {
    font-size: 20px;
}